body {
  height: 100%;
  width: 100%;
  font-family: 'Raleway', sans-serif;
  background: url(./assets/7135.jpg) no-repeat;
  background-size: cover;
  margin: 0;
}

.logo-navbar {
  position: relative;
  top: 5px;
  left: 80px;
  height: 80px;
  width: 65px;
}

.heading-section {
  border-right: 1px solid #74CEDD;
  background-color: #1C1616;
  -webkit-transform: rotate(20deg);
      -ms-transform: rotate(20deg);
          transform: rotate(20deg);
  position: absolute;
  top: -150px;
  left: -114px;
  height: 90%;
  width: 220px;
  z-index: 1;
}

.heading-wrapper {
  -webkit-transform: rotate(-20deg);
      -ms-transform: rotate(-20deg);
          transform: rotate(-20deg);
  position: absolute;
  top: 140px;
  left: 70px;
}

.main-content-div {
  position: absolute;
  top: 115px;
  right: -160px;
  padding: 50px;
  width: 80%;
  max-height: 413px;
  background-color: #1C1616;
  border: 1px solid #74CEDD;
  color: #FDFDFD;
  font-size: 20px;
  -webkit-transform: skewX(-20deg);
      -ms-transform: skewX(-20deg);
          transform: skewX(-20deg);
  overflow-y: scroll;
  overflow-x: hidden;
}


@media screen and (max-width: 810px) {
  .main-content-div {
    right: -130px;
    width: 90%;
  }
}

@media screen and (max-width: 710px) {
  body {
    background-size: auto;
  }
}

@media screen and (max-width: 414px) {
  .app-wrapper {
    width: 100%;
    margin: 0;
  }

  .main-content-div {
    -webkit-transform: skewX(0);
        -ms-transform: skewX(0);
            transform: skewX(0);
    position: static;
    width: 90%;
    margin: auto;
    margin-top: 35px;
    padding: 0;
    max-height: none;
  }

  .heading-section {
    position: relative;
    -webkit-transform: rotate(0deg);
        -ms-transform: rotate(0deg);
            transform: rotate(0deg);
    width: 100%;
    border-right: none;
    border-bottom: 1px solid #74CEDD;
    top: 0;
    left: 0;
  }

  .heading-wrapper {
    -webkit-transform: rotate(0deg);
        -ms-transform: rotate(0deg);
            transform: rotate(0deg);
    position: static;
  }

  .logo-navbar {
    left: 5px;
    top: 0;
    display: block;
    margin: auto;
    padding: 10px;
  }
}



