.resume-container {
  height: 413px;
  width: 100%;
}

.profile-div {
  color: #FDFDFD;
  background-color: #1C1616;
  top: 0;
  left: 0;
}

.resume-text {
  -webkit-transform: skewX(20deg);
      -ms-transform: skewX(20deg);
          transform: skewX(20deg);
  padding-left: 25px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: start;
      justify-content: flex-start;
}

.profile-content {
  font-size: 12px;
  font-weight: 300;
}

.profile-title {
  margin-bottom: 0;
}

.title {
  margin-top: 15px;
  margin-bottom: 5px;
}

.resume-content {
  position: absolute;
  width: 50%;
  border: 1px solid #74CEDD;
  padding: 10px;
  height: 150px;
}

.skills-div {
  border: 1px solid #74CEDD;
  width: 50%;
  top: 171px;
  right: 83px;
  padding-left: 30px;
}

.education-div {
  top: 342px;
  left: 0;
}

.small-content {
  font-weight: 300;
  font-size: 12px;
}

.skills-list {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  padding-left: 0;
  list-style: none;
  margin-top: 0;
}

.skills-list-item {
  width: 26%;
  padding-top: 5px;
}

@media screen and (max-width: 414px) {
  .resume-container {
    height: auto;
  }

  .resume-content {
    position: static;
    height: auto;
    width: 100%;
    padding: 0;
    text-align: center;
  }

  .resume-text {
    -webkit-transform: skewX(0);
        -ms-transform: skewX(0);
            transform: skewX(0);
    padding-left: 0;
  }

  .skills-div {
    padding-left: 0;
  }
}



