.navbar {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.navlink {
  text-decoration: none;
  color: #1C1616;
  list-style: none;
  margin-top: 20px;
  font-weight: 300;
  background-color: #FDFDFD;
  color: black;
  border: 1px solid #1C1616;
  -webkit-transform: skewX(-20deg);
      -ms-transform: skewX(-20deg);
          transform: skewX(-20deg);
  position: relative;
  left: -11px;
}

.navlink:hover {
  background-color: #74CEDD;
  -webkit-transition: background-color 0.5s;
  -o-transition: background-color 0.5s;
  transition: background-color 0.5s;
  color: #FDFDFD;
}

.navlink.active {
  background-color: #74CEDD;
  -webkit-transition: width 0.5s;
  -o-transition: width 0.5s;
  transition: width 0.5s;
}

.portfolio {
  width: 172px;
  -webkit-transition: width 0.5s;
  -o-transition: width 0.5s;
  transition: width 0.5s;
}

.contact {
  width: 153px;
  -webkit-transition: width 0.5s;
  -o-transition: width 0.5s;
  transition: width 0.5s;
}

.resume {
  width: 135px;
  -webkit-transition: width 0.5s;
  -o-transition: width 0.5s;
  transition: width 0.5s;
}

.about {
  width: 116px;
  -webkit-transition: width 0.5s;
  -o-transition: width 0.5s;
  transition: width 0.5s;
}

.portfolio:hover, .portfolio.active {
  width: 200px;
  -webkit-transition: width 0.5s;
  -o-transition: width 0.5s;
  transition: width 0.5s;
}

.contact:hover, .contact.active {
  width: 182px;
  -webkit-transition: width 0.5s;
  -o-transition: width 0.5s;
  transition: width 0.5s;
}

.resume:hover, .resume.active {
  width: 164px;
  -webkit-transition: width 0.5s;
  -o-transition: width 0.5s;
  transition: width 0.5s;
}

.about:hover, .about.active {
  width: 147px;
  -webkit-transition: width 0.5s;
  -o-transition: width 0.5s;
  transition: width 0.5s;
}

span {
  position: relative;
  display: block;
  -webkit-transform: skewX(20deg);
      -ms-transform: skewX(20deg);
          transform: skewX(20deg);
  padding: 5px;
  left: 49px;
}

@media screen and (max-width: 414px) {
  .navlink {
    position: static;
    background-color: #1C1616;
    color: #FDFDFD;
    border-top: 1px solid #74CEDD;
    width: 100%;
    -webkit-transform: skewX(0deg);
        -ms-transform: skewX(0deg);
            transform: skewX(0deg);
    margin-top: 0;
  }

  .about {
    border-bottom: 1px solid #74CEDD;
  }

  .navlink.active, .navlink:hover {
    width: 100%;
  }

  span {
    text-align: center;
    left: 0;
    -webkit-transform: skewX(0);
        -ms-transform: skewX(0);
            transform: skewX(0);
  }
}



